.chart {
	width: 80%;
	margin: 0 auto;
	margin-top: 60px;
	.MuiFormControl-root {
		// float: left;
		label {
			font-size: 20px;
			color: #005776;
			font-weight: 600;
		}
		.MuiInputBase-root {
			border: 1px solid #005776;
			width: 270px;
			border-radius: 10px;
			margin-top: 30px;
			background: white;
			&:before {
				content: none;
			}
			select {
				padding-right: 24px;
				content: none;
				background: none;
				padding-left: 10px;
			}
			&:after {
				content: none;
			}
		}
	}
	.filter_t {
		.filter-overview {
			position: relative;
			.dates {
				position: absolute;
				top: 80px;
				margin: 6px;
				width: 273px;
				.rs-picker-toggle {
					&:active {
						border: 1px solid #015478;
					}
					&:hover {
						border: 1px solid #015478;
					}
				}
				.rs-btn {
					padding-top: 6px;
					padding-bottom: 6px;
					border-radius: 10px;
				}
				span {
					color: #015478;
				}
				.rs-picker-toggle-placeholder {
					font-size: 0;
					&::before {
						content: 'Tùy chọn';
						color: #015478;
						font-size: 14px;
					}
				}
			}
		}
	}
}
