.Dashboard {
	h1 {
		margin: 20px;
		color: #005776;
	}
	> div {
		padding: 25px 32px;
		width: 80%;
		margin: 0 auto;
		background: white;
		border-radius: 10px;
		box-shadow: 3px 3px 5px 0px #888888;

		.MuiPaper-rounded {
			background: #f28300;
			border-radius: 10px;
			.title {
				display: block;
				color: #f1f1f1;
				margin-bottom: 5px;
				font-size: 18px;
			}
			.number {
				color: #005776;
				font-size: 25px;
				font-weight: 600;
			}
		}
	}
}
