.filter {
	margin-top: 20px;
	background: white;
	padding: 10p;
	padding: 20px 30px;
	border-radius: 10px;
	box-shadow: 3px 3px 5px 0px #888888;
	> div {
		width: 50%;
	}
	.MuiPaper-rounded {
		box-shadow: none;
	}
	.MuiInputBase-root:before {
		content: none;
	}
	.MuiInputBase-root::after {
		content: none;
	}
	.MuiInputBase-root {
		input {
			border: 1px solid #015478;
			border-radius: 7px;
			padding-left: 8px;
			&::placeholder {
				color: #015478;
				font-style: italic;
				opacity: 1;
			}
		}
	}
	.MuiInputLabel-formControl {
		top: -16px;
		left: 10px;
	}
	.MuiInput-formControl {
		margin-top: 0;
	}
	.MuiFormControl-root {
		margin: 0px;
		min-width: 173px;
		border: 1px solid #015478;
		border-radius: 7px;
		padding-left: 8px;
		label {
			color: #015478;
			font-style: italic;
			.Mui-focused {
				display: none;
			}
		}
	}
	.rs-btn {
		float: left;
		border: 1px solid #015478;
		span {
			color: #015478;
		}
	}
	.MuiPaper-root {
		width: 205px;
	}
	.MuiGrid-spacing-xs-3 {
		.MuiGrid-item {
			padding: 0;
		}
	}
	button {
		background: #015478;
		color: white;
		width: 173px;
		box-shadow: none;
	}
	.Registration-Button {
		button {
			background: #66b334;
			font-weight: 600;
			padding: 12px;
			border: none;
		}
	}
}

.table-wrapper {
	padding: 20px 30px 40px 30px;
	background: white;
	border-radius: 10px;
	margin-top: 20px;
	box-shadow: 3px 3px 5px 0px #888888;
	.MuiPaper-root {
		border: none;
		box-shadow: none;
	}
	.TableList__content {
		.MuiToolbar-gutters {
			display: none;
		}
	}
}

.MuiButton-root.button_blue {
	background: #5ed6f6;
	color: white;
	border-radius: 7px;
	box-shadow: 3px 3px 5px 0px #888888;
	border: none;
	margin-right: 15px;
	font-size: 14px;
	text-transform: capitalize;
	&:hover {
		color: #5ed6f6;
	}
}
svg {
	color: #015478;
}
.makeStyles-paper-24 {
	padding: 0;
	border: none;
	border-radius: 10px;
}

.form-modal {
	border-radius: 10px;
	.form-modal__title {
		background: #f28300;
		text-align: center;
		color: white;
		padding: 10px 0px;
		border-top-left-radius: 10px;
		border-top-right-radius: 10px;
		h3 {
			font-size: 20px;
		}
	}
	.form-modal__content {
		padding: 0px 20px 20px 20px;
		.note {
			padding-top: 15px;
			padding-bottom: 15px;
			color: #015478;
			font-style: italic;
			padding-left: 20px;
			font-style: normal;
			padding-left: 0;
			p {
			}
			span {
				.MuiButtonBase-root {
					padding-left: 0;
				}
			}
		}
		.form {
			.MuiFormControl-root {
				display: block;
				margin-bottom: 10px;
				.MuiInputBase-root {
					width: 100%;
					margin-bottom: 10px;
					margin-top: 20px;
				}
				label {
					color: #015478;
					font-size: 16px;
					font-weight: 600;
				}
			}
			.form_button {
				text-align: center;
				button {
					border: 1px solid #f28300;
					color: #f28300;
					&:first-child {
						margin-right: 10px;
					}
					&:hover {
						background: #f28300;
						color: white;
					}
				}
			}
			.form_file {
				background: #ffe9d4;
				padding: 20px;
				border-radius: 10px;
				margin-bottom: 20px;
				.form_file_border {
					border: 1px solid #015478;
					padding: 15px;
					border-radius: 10px;
					background: white;
					h3 {
						font-size: 19px;
						text-align: center;
						margin-bottom: 20px;
						color: #015478;
					}
				}
			}
			.role {
				width: 100%;
				margin-bottom: 10px;
				border: 1px solid #015478;
				border-radius: 7px;
				padding-left: 15px;
				&.MuiInput-underline::before {
					border-bottom: none;
					svg {
						border-bottom: none;
					}
				}
			}
			.role-lable {
				margin-bottom: 10px;
				margin-top: 26px;
			}
			.MuiInputLabel-animated {
				font-size: 22px;
				color: #015478;
				font-weight: 600;
			}
		}
	}
}
