.page-wrapper {
	display: flex;
	.page-left {
		background-color: #015478;
		.sider_bar {
			width: 214px;
			height: 100vh;
			text-align: center;
			.sider_bar__title {
				border-bottom: 1px solid #f1f1f1;
				height: 65px;
				h2 {
					color: #f1f1f1;
					font-size: 22px;
					line-height: 65px;
				}
			}
			.sider_bar__content {
				.sider_bar__content--list {
					margin-bottom: 10px;
					&:first-child {
						margin-top: 34px;
					}
					button {
						width: 192px;
						border: 2px solid #f1f1f1;
						border-radius: 30px;
						padding: 8px 0px;
						background: none;
						&:hover {
							background: #f1f1f1;
							a {
								color: #005776;
							}
						}
						a {
							color: #f1f1f1;
						}
					}
				}
			}
		}
	}
	.page-right {
		width: 100%;
		background: #c7dde3;
	}
}
.MuiMenu-paper {
	margin-top: 49px;
	border-radius: 10px !important;
	font-size: 13px;
	ul {
		padding-top: 0;
		li:first-child {
			display: block;
			background: #015478;
			padding-top: 20px;
			padding-bottom: 20px;
			span {
				display: block;
				color: white;
			}
			span:first-child {
				font-size: 19px;
				font-weight: 600;
			}
		}
		li {
			span:first-child {
				margin-right: 10px;
			}
		}
	}
}
