.AccountInformation_wrapper {
	width: 600px;
	padding: 50px 30px 30px 30px;
	background: white;
	position: absolute;
	left: 50%;
	top: 50%;
	transform: translate(-50%, -50%);
	border-radius: 10px;
	.title {
		text-align: center;
		margin-bottom: 40px;
		color: #015478;
	}
	.AccountInformation {
		display: flex;
		.AccountInformation__avata {
			margin: 0 auto;
			svg {
				font-size: 40px;
				color: #015478;
			}
		}
		.AccountInformation__form {
			margin: 0 auto;
			.MuiFormControl-root {
				display: block;
				margin-bottom: 10px;
				margin: 0;
				padding-bottom: 20px;
			}
			.Mui-disabled {
				display: block;
				margin-bottom: 20px;
			}
			input {
				width: 300px;
			}
			.MuiInput-underline:after {
				border-bottom: 1px solid #015478;
			}
			> button {
				float: right;
				background: #015478;
				padding: 8px 20px;
				color: white;
				font-weight: 600;
			}
			label {
				color: #015478;
				font-size: 18px;
				font-weight: 600;
			}
			.Modal__password {
				button {
					padding: 10px 20px;
					border-radius: 10px;
					border: 1px solid #015478;
					background: white;
					color: #015478;
				}
			}
		}
	}
}
